import { glob } from 'glob'
import { readFile, writeFile, remove } from 'fs-extra'
import { resolve, join } from 'path'
import type { BuilderConfig, MultiFormatBuildConfig, CSSBuildStrategy } from '../types/config'
import { LibraryBuilder } from './LibraryBuilder'

/**
 * TDesign È£éÊ†ºÁöÑÊûÑÂª∫Âô®
 */
export class TDesignStyleBuilder extends LibraryBuilder {
  
  /**
   * ÊûÑÂª∫ ES Ê®°ÂùóÔºàÊîØÊåÅ tree-shakingÔºâ
   */
  async buildEs(config: BuilderConfig): Promise<void> {
    console.log('üî® ÊûÑÂª∫ ES Ê®°Âùó...')
    
    // ÂÖàÊûÑÂª∫ CSS
    await this.buildCss(config)
    
    // ÂÜçÊûÑÂª∫ÁªÑ‰ª∂
    await this.buildComponents(config, 'es')
    
    console.log('‚úÖ ES Ê®°ÂùóÊûÑÂª∫ÂÆåÊàê')
  }

  /**
   * ÊûÑÂª∫ ESM Ê®°ÂùóÔºà‰øùÁïôÊ∫êÁ†ÅÊ†∑ÂºèÂºïÁî®Ôºâ
   */
  async buildEsm(config: BuilderConfig): Promise<void> {
    console.log('üî® ÊûÑÂª∫ ESM Ê®°Âùó...')
    
    const enhancedConfig: BuilderConfig = {
      ...config,
      cssStrategy: 'source',
      output: {
        ...config.output,
        dir: 'esm/',
        format: 'esm',
        chunkFileNames: '_chunks/dep-[hash].js'
      }
    }
    
    await this.build(enhancedConfig)
    
    // ÊõøÊç¢ @tdesign/common-style ‰∏∫Áõ∏ÂØπË∑ØÂæÑ
    await this.replaceCommonStyleImports('esm')
    
    console.log('‚úÖ ESM Ê®°ÂùóÊûÑÂª∫ÂÆåÊàê')
  }

  /**
   * ÊûÑÂª∫ Lib Ê†ºÂºè
   */
  async buildLib(config: BuilderConfig): Promise<void> {
    console.log('üî® ÊûÑÂª∫ Lib Ê†ºÂºè...')
    
    const enhancedConfig: BuilderConfig = {
      ...config,
      cssStrategy: 'ignore',
      output: {
        ...config.output,
        dir: 'lib/',
        format: 'esm',
        chunkFileNames: '_chunks/dep-[hash].js'
      }
    }
    
    await this.build(enhancedConfig)
    
    console.log('‚úÖ Lib Ê†ºÂºèÊûÑÂª∫ÂÆåÊàê')
  }

  /**
   * ÊûÑÂª∫ CJS Ê†ºÂºè
   */
  async buildCjs(config: BuilderConfig): Promise<void> {
    console.log('üî® ÊûÑÂª∫ CJS Ê†ºÂºè...')
    
    const enhancedConfig: BuilderConfig = {
      ...config,
      cssStrategy: 'ignore',
      output: {
        ...config.output,
        dir: 'cjs/',
        format: 'cjs',
        exports: 'named',
        chunkFileNames: '_chunks/dep-[hash].js'
      }
    }
    
    await this.build(enhancedConfig)
    
    console.log('‚úÖ CJS Ê†ºÂºèÊûÑÂª∫ÂÆåÊàê')
  }

  /**
   * ÊûÑÂª∫ UMD Ê†ºÂºè
   */
  async buildUmd(config: BuilderConfig, isMin = false): Promise<void> {
    console.log(`üî® ÊûÑÂª∫ UMD Ê†ºÂºè${isMin ? 'ÔºàÂéãÁº©ÁâàÔºâ' : ''}...`)
    
    const filename = isMin ? 'index.min.js' : 'index.js'
    const umdName = config.umd?.name || 'LDesign'
    
    const enhancedConfig: BuilderConfig = {
      ...config,
      cssStrategy: 'single',
      minify: isMin,
      output: {
        ...config.output,
        file: `dist/${filename}`,
        format: 'umd',
        name: umdName,
        exports: 'named',
        globals: { vue: 'Vue', ...config.globals }
      }
    }
    
    await this.build(enhancedConfig)
    
    console.log(`‚úÖ UMD Ê†ºÂºè${isMin ? 'ÔºàÂéãÁº©ÁâàÔºâ' : ''}ÊûÑÂª∫ÂÆåÊàê`)
  }

  /**
   * ÊûÑÂª∫ CSS Êñá‰ª∂
   */
  private async buildCss(config: BuilderConfig): Promise<void> {
    console.log('üé® ÊûÑÂª∫ CSS Êñá‰ª∂...')
    
    try {
      // ËøôÈáåÂèØ‰ª•Ê∑ªÂä†ÂÖ∑‰ΩìÁöÑ CSS ÊûÑÂª∫ÈÄªËæë
      // ‰æãÂ¶Ç‰ΩøÁî® rollup ÊûÑÂª∫Ê†∑ÂºèÊñá‰ª∂
      const cssConfig: BuilderConfig = {
        ...config,
        input: ['src/**/style/index.js'],
        output: {
          dir: 'es/',
          assetFileNames: '[name].css'
        },
        cssStrategy: 'multi'
      }
      
      await this.build(cssConfig)
      
      console.log('‚úÖ CSS Êñá‰ª∂ÊûÑÂª∫ÂÆåÊàê')
    } catch (error) {
      console.warn('‚ö†Ô∏è CSS ÊûÑÂª∫Â§±Ë¥•:', error)
    }
  }

  /**
   * ÊûÑÂª∫ÁªÑ‰ª∂
   */
  private async buildComponents(config: BuilderConfig, format: string): Promise<void> {
    console.log(`üß© ÊûÑÂª∫ÁªÑ‰ª∂ (${format})...`)
    
    const componentConfig: BuilderConfig = {
      ...config,
      cssStrategy: 'multi',
      output: {
        ...config.output,
        dir: `${format}/`,
        format: 'esm',
        sourcemap: true,
        entryFileNames: '[name].mjs',
        chunkFileNames: '_chunks/dep-[hash].mjs'
      }
    }
    
    await this.build(componentConfig)
    
    // Ê∏ÖÁêÜ‰∏çÈúÄË¶ÅÁöÑÊñá‰ª∂
    await this.cleanupFiles(format)
    
    console.log(`‚úÖ ÁªÑ‰ª∂ÊûÑÂª∫ÂÆåÊàê (${format})`)
  }

  /**
   * ÊõøÊç¢ÈÄöÁî®Ê†∑ÂºèÂØºÂÖ•
   */
  private async replaceCommonStyleImports(dir: string): Promise<void> {
    console.log(`üîÑ ÊõøÊç¢Ê†∑ÂºèÂØºÂÖ• (${dir})...`)
    
    try {
      const files = await glob(`${dir}/**/*.*`)
      const rewritePromises = files.map(async (filePath) => {
        const content = await readFile(filePath, 'utf8')
        const newContent = content.replace(
          /@ldesign\/common-style/g, 
          'ldesign-vue-next/esm/common/style'
        )
        if (content !== newContent) {
          await writeFile(filePath, newContent, 'utf8')
        }
      })
      
      await Promise.all(rewritePromises)
      console.log(`‚úÖ Ê†∑ÂºèÂØºÂÖ•ÊõøÊç¢ÂÆåÊàê (${dir})`)
    } catch (error) {
      console.warn(`‚ö†Ô∏è Ê†∑ÂºèÂØºÂÖ•ÊõøÊç¢Â§±Ë¥• (${dir}):`, error)
    }
  }

  /**
   * Ê∏ÖÁêÜ‰∏çÈúÄË¶ÅÁöÑÊñá‰ª∂
   */
  private async cleanupFiles(dir: string): Promise<void> {
    try {
      const files = await glob(`${dir}/**/style/index.js`)
      const removePromises = files.map(async (filePath) => {
        await remove(filePath)
      })
      
      await Promise.all(removePromises)
    } catch (error) {
      console.warn(`‚ö†Ô∏è Êñá‰ª∂Ê∏ÖÁêÜÂ§±Ë¥• (${dir}):`, error)
    }
  }

  /**
   * Âà†Èô§ËæìÂá∫ÁõÆÂΩï
   */
  async deleteOutput(): Promise<void> {
    console.log('üóëÔ∏è Ê∏ÖÁêÜËæìÂá∫ÁõÆÂΩï...')
    
    const dirs = ['es', 'esm', 'lib', 'cjs', 'dist']
    const removePromises = dirs.map(async (dir) => {
      try {
        await remove(dir)
      } catch (error) {
        // ÂøΩÁï•Âà†Èô§Â§±Ë¥•ÁöÑÈîôËØØ
      }
    })
    
    await Promise.all(removePromises)
    console.log('‚úÖ ËæìÂá∫ÁõÆÂΩïÊ∏ÖÁêÜÂÆåÊàê')
  }

  /**
   * ÊûÑÂª∫ÊâÄÊúâÊ†ºÂºèÔºàTDesign È£éÊ†ºÔºâ
   */
  async buildComponents(config: BuilderConfig): Promise<void> {
    console.log('üöÄ ÂºÄÂßã TDesign È£éÊ†ºÁöÑÂ§öÊ†ºÂºèÊûÑÂª∫...')
    
    // Ê∏ÖÁêÜËæìÂá∫ÁõÆÂΩï
    await this.deleteOutput()
    
    const multiFormat = config.multiFormat || {
      es: true,
      esm: true,
      lib: true,
      cjs: true,
      umd: true,
      umdMin: true
    }
    
    // ÊåâÈ°∫Â∫èÊûÑÂª∫ÂêÑÁßçÊ†ºÂºè
    if (multiFormat.es) await this.buildEs(config)
    if (multiFormat.esm) await this.buildEsm(config)
    if (multiFormat.lib) await this.buildLib(config)
    if (multiFormat.cjs) await this.buildCjs(config)
    if (multiFormat.umd) await this.buildUmd(config)
    if (multiFormat.umdMin) await this.buildUmd(config, true)
    
    console.log('üéâ TDesign È£éÊ†ºÁöÑÂ§öÊ†ºÂºèÊûÑÂª∫ÂÆåÊàêÔºÅ')
  }
}
